/* Loading.css */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  color: blue;
  z-index: 1000;
}

.loading-text {
  font-size: 1.5rem;
  margin-bottom: 20px;
  font-weight: bold;
}

.loading-bar {
  width: 80%;
  height: 10px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 5px;
  overflow: hidden;
  position: relative;
}

.loading-bar-progress {
  width: 0;
  height: 100%;
  background: blue;
  animation: loading 2s infinite;
}

@keyframes loading {
  0% {
    width: 0;
  }
  50% {
    width: 80%;
  }
  100% {
    width: 100%;
  }
}
